body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

:root {
  --color-active: #00acdc;
}

.header {
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 500;
}

.menu_logo {
  flex-grow: 1;
  width: 40px;
  height: 40px;
}

.header_menu {
  margin: 0 50px;
}

.menu {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.nav_list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav_list_item:not(:last-child) {
  margin-right: 20px;
}

.nav_link {
  color: #000;
  height: 60px;
  line-height: 60px;
  text-decoration: none;
  display: flex;
}

.nav_link_blue, .nav_link:hover {
  color: var(--color-active);
}

.is-active {
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--color-active);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
}
